.card-course {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, .15);
  overflow: hidden;
  position: relative;
  background-color: #fff;
  width: 100%;
  min-height: 100%;
  border-radius: 3px;
  border: 0 solid #818a91;
  transition: all .25s;
  color: #222222;

  &:hover {
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, .15);
  }

  &__content {
    padding: 0 30px;
  }

  &__faculty {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #818a91;
    color: #fff;
    font-size: 12px;
    padding: 0.6em 1.2em;
    line-height: 1;
    font-weight: 400;
    border-radius: 40px;
    margin: 20px;
    text-transform: uppercase;
    font-family: "Nunito", sans-serif;
  }

  &__heading,
  .heading {
    color: #222222;
    font-size: 1.4rem;
    font-weight: bold;
    margin: 30px 0;
  }

  &__imagen {
    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  &__button {

    &-link {
      margin-bottom: 20px;
      display: inline-block;
      text-transform: uppercase;
      font-size: 0.8rem;
      font-weight: 700;
      margin-top: 30px;
    }
  }
}