/* Create a mixin for paragraph styles that can be implemented
 * in components with other classnames.
*/
@mixin paragraph($font-size: 1rem, $margin: 0 0 1rem 0) {
  margin: $margin;
  font-family: $font-body;
  //font-size: $font-size;
}

@mixin paragraphSmall($font-size: 0.85rem, $margin: 0 0 1rem 0) {
  margin: $margin;
  font-family: $font-body;
  font-size: $font-size;
}

.paragraph {
  @include paragraph;

  &--small {
    @include paragraphSmall;
  }
}

.blockquote {
  font-family: $font-body;
  font-style: italic;
  border-left: solid 3px clr(accent);
  margin-left: 1em;
  padding-left: 1em;
}

.hr {
  border-style: solid;
  border-width: 1px 0 0;
  color: currentColor;
}

/* TODO: BEM selectors should be applied to wysiwyg-created content */

.text-long {
  p {
    @extend .paragraph;

    &.paragraph--small {
      @include paragraphSmall;
    }
  }

  blockquote {
    @extend .blockquote;
  }

  hr {
    @extend .hr;
  }
}
