.tabs__link,
.tabs-component__link,
.tabs__link--local-tasks {
  background-color: clr(muted);
  border: 1px solid clr(highlight-high);
  border-bottom: none;
  color: clr(text);
  display: block;
  font-size: 1.1rem;
  font-weight: 600;
  padding: 1rem;
  text-align: center;
  text-decoration: none;
  transition: color 0.3s;
  font-family: $font-heading;

  @include large {
    border-bottom: 1px solid clr(highlight-high);
    display: block;
    font-size: 1.1rem;
    padding: 1rem;
    margin-bottom: 0.8rem;
    position: relative;
    top: 1px;
    min-width: 280px;
    max-width: 280px;
  }

  &.is-active {
    background-color: clr(text);
    border: 1px solid clr(text);
    color: clr(text-inverse);

    @include large {
      background-color: clr(background);
      border: 1px solid clr(highlight-high);
      border-bottom: 1px solid clr(highlight-high);
      border-left: none;
      color: clr(text);
    }
  }
}

.tab-vertical {
  .nav-pills .nav-link {
    background-color: #dbdbe4;
    border: 0;
    border-radius: 0;
    min-width: 290px;
    color: clr(text);
    text-align: left;
    padding: 1rem;

    &.active {
      background-color: var(--main-page-color);
      color: clr(text-inverse);

      .tab-icon {
        svg {
          fill: $white;
        }
      }
    }
  }

  .tab-content {
    width: 100%;
    border: 1px solid lightgray;
    padding: 1rem 2rem;
  }

  .tab-icon {
    svg {
      max-width: 20px;
      max-height: 20px;
    }
  }
}

.tabs__nav {
  border: none;

  &__link {
    border: 1px solid var(--c-highlight-high);
  }

  .tabs__icon {
    font-size: 1.8em;
  }
}

.tabs--horizontal,
.tabs-component--horizontal {
  .is-active {
    .tabs__icon {
      filter: brightness(21.5);
    }
  }

  @include large {
    flex-direction: column;

    .tabs__nav {
      border: 0;
      box-shadow: 0px 0px 4px 0px #000000;
      min-width: 100%;
      max-width: 100%;
      margin-bottom: 1em;
      flex-direction: row;

      li {
        width: 100%;
      }

      .tabs__link,
      .tabs-component__link {
        margin: 0 !important;
        min-width: 100%;
        max-width: 100%;
        padding: 0.8em;

        &:hover {
          color: #000000;
        }
      }
    }
  }
}
