.card-resources {
  width: 100%;

  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #D8D8D8;
    cursor: pointer;

    @include medium {
      gap: 40px;
    }

    &-left {
      width: 100%;
      display: flex;
      align-items: center;
      gap: 10px;

      svg {
        width: 20px;
        height: auto;
        object-fit: cover;
      }

      &-description {
        display: table-cell;
        color: #333;
        font-size: 16px;
        font-weight: 400;
        vertical-align: middle;
        padding: 0 16px;
      }
    }

    &-right {
      display: flex;
      flex-direction: column-reverse;
      align-items: flex-end;
      gap: 10px;

      @include medium {
        width: 100%;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        padding-right: 50px;
      }



      &-description {
        padding: 0 15px;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 1px;
        line-height: 24px;
        background: #f0f0f0;
        color: #000;
        border-radius: 3px;
        width: max-content;
      }

      &-resource {
        padding: 0 15px;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 1px;
        line-height: 24px;
        background-color: #3d64ff;
        color: white;
        border-radius: 3px;
        width: max-content;
      }
    }

  }

  &__modal {

    &-header {
      border: none;
      padding: 1rem 1rem 0;

      .button-modal {
        color: #1B3263;
        background-color: #FABA5B;
        border: none;
        width: 35px;
        height: auto;
        aspect-ratio: 1/1;

        &:hover {
          color: #FFFFFF;
          background-color: #1B3263;
          animation-name: elementor-animation-pulse;
          animation-duration: 1s;
          animation-timing-function: linear;
          animation-iteration-count: infinite;

          @keyframes elementor-animation-pulse {
            25% {
              transform: scale(1.1)
            }

            75% {
              transform: scale(0.9)
            }
          }
        }
      }
    }

    &-body {
      iframe {
        width: 100%;
        height: auto;
        aspect-ratio: 16/9;
      }
    }
  }

}