.card-course-simple {
  width: 100%;
  border-radius: 10px;
  transition: all .2s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  border: 1px solid #fff;

  &__imagen {
    width: 100%;
    height: 250px;

    @include medium {
      height: 180px;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      object-fit: cover;
      object-position: center;
      height: 250px;
      transition: all 0.3s ease;

      @include medium {
        height: 180px;
      }

      .card-course-simple:hover &,
      .card-course-simple:focus & {
        height: 100%;
        filter: brightness(0.8);
      }

    }
  }

  &__button {
    position: absolute;
    top: 215px;
    right: 15px;

    @include medium {
      top: 145px;
    }

    &-link {
      background-color: #4663f6;
      color: white;
      border-radius: 50%;
      width: 65px;
      height: auto;
      aspect-ratio: 1 / 1;
      display: grid;
      place-items: center;
      font-size: 0.9rem;
      font-weight: 700;
      transition: all 0.3s ease;

      .card-course-simple:hover & {
        -ms-transform: scale(1.2) translateY(-10px);
        -moz-transform: scale(1.2) translateY(-10px);
        -o-transform: scale(1.2) translateY(-10px);
        -webkit-transform: scale(1.2) translateY(-10px);
        transform: scale(1.2) translateY(-10px);
        color: $white;
      }

    }
  }

  &__content {
    background-color: #EFECE6;
    padding: 25px 20px 30px;

    .card-course-simple:hover & {
      position: relative;
      background-color: transparent;
    }


    &-title {

      &-heading {
        color: #1B3263;
        font-size: 1rem;
        font-weight: 700;
        line-height: 1.7;

        .card-course-simple:hover & {
          color: $white;
        }
      }
    }

  }
}