.landing-negotiation {
  &__imagen {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 400px;

    @include medium {
      height: 500px;
    }

    >div {
      width: 100%;
      height: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: brightness(0.8);
    }

    &-title {
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      @include layout-max-width;
      margin: 0 auto;

      &-heading {
        color: $white;
        font-family: 'Montserrat';
        line-height: 1.7;
        text-transform: none;
        font-size: 1.75rem;
        font-weight: 700;
        margin-bottom: 1rem;
        padding-left: 1rem;

        @include medium {
          width: 66%;
        }

        @include large {
          padding-left: 0.1rem;
          font-size: 2rem;
        }
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 15px;

    @include medium {
      flex-direction: row;
      justify-content: space-between;
      gap: 20px;
    }

    &-container {
      padding: 30px 20px 60px;
      color: #1a3263;

      &-icon {
        display: flex;
        gap: 5px;
      }
    }

    &-left {
      margin-top: 54px;
      flex: 0 1 66%;
    }

    &-right {
      position: relative;
      background: #e8e2db;
      flex: 0 1 33%;
      border-radius: 5px;
      box-shadow: 0 5px 40px rgba(0, 0, 0, 0.05);
      overflow: hidden;
      padding: 0;
      box-sizing: border-box;
      margin-bottom: 60px;
      height: max-content;

      @include medium {
        margin-top: -300px;
      }

      &-subheading {
        font-weight: bold;
        font-size: 1.5rem;
        color: #1a3263;
        text-transform: none;
        margin-bottom: 20px;
      }
    }

    &-bottom {
      display: grid;
      grid-template-columns: 1fr;
      gap: 15px;
      margin-top: 40px;

      @include medium {
        grid-template-columns: repeat(3, 1fr);
      }

      @include large {
        grid-template-columns: repeat(4, 25%);
      }
    }
  }

  .landing-teacher__image {
    flex: 0 0 100px;
  }

  .tab-bootstap--course .nav-link {
    margin: 0 auto;
  }

  .landing-teacher__heading {
    font-size: 1.1rem;
  }

  .tab-bootstap {
    display: block;
    margin: 0 auto;

    @include medium {
      width: 90%;
    }

    .tab-content {
      padding: 5px 0;
    }
  }

  .tab-accordion-mobile {
    display: none;
  }

  .title-interest {
    font-size: 1.4rem;
    margin-top: 2rem;
  }

  .landing-teacher {
    width: 100%;
    background-color: transparent;
    padding: 40px 10px 40px;
  }
}