.landing-teacher {
  width: 100%;
  padding: 50px 15px;

  @include medium {
    padding: 70px 10px 40px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 30px;
    min-height: 345px;
    max-width: 1140px;
    margin: 0 auto;

    @include medium {
      flex-direction: row;
      justify-content: space-between;
      gap: 35px;
    }
  }

  &__heading {
    color: #1a3263;
    font-weight: 700;
    font-size: 2.1rem;
    padding-bottom: 18px;
    text-transform: none;
  }

  p {
    font-weight: 300;
  }

  &__image {
    @include medium {
      flex: 0 0 270px;
    }

    img {
      width: 220px;
      height: auto;
      object-fit: cover;
      aspect-ratio: 1/1;
      border-radius: 50%;
      margin: 0 auto;

      @include medium {
        width: 100%;
      }
    }
  }

  &__content {
    flex: 0 1 100%;
  }
}