/*
 * Generic header styles:
 * All arguments are optional. If not defined, the defaults below will be used
*/

$color-heading: clr(text);

@mixin heading-xl(
  $font-family: $font-heading-h1,
  $font-size: 3.4375rem,
  $line-height: 1.2,
  $color: $color-heading,
  $color-link: inherit,
  $color-link-hover: clr(primary),
  $font-weight: 400,
  $margin: 0 0 0.25rem
) {
  color: $color;
  font-family: $font-family;
  font-weight: $font-weight;
  font-style: normal;
  font-size: $font-size;
  line-height: $line-height;
  margin: $margin;
  width: auto;
  font-display: block;

  &__link {
    color: $color-link;
    text-decoration: none;

    &:hover {
      color: $color-link-hover;
    }
  }
}

