@mixin subheading-label {
  font-weight: 100;
  text-transform: uppercase;
  margin: $space-1 0;
}

.h1 {
  @include heading-xl($font-size: 2.75rem, $color: inherit);

  @include medium {
      @include heading-xl($font-size: 3.4375rem, $color: inherit);
  }


  &--article {
    @include heading-large($font-size: 2.5rem, $color: $red);
    font-family: 'Nunito', sans-serif;
    font-size: 1.8em;
    font-weight: 500;
    text-align: center;
    padding: 1rem 0 0;

    @include large {
      @include heading-large($font-size: 3rem, $color: $red, $margin: 0);
      font-family: 'Nunito', sans-serif;
      font-size: 1.8em;
      font-weight: 500;
    }
  }

  &--color-white {
    @include heading-xl($color: $white);
    font-family: 'Nunito', sans-serif;
    font-size: 1.8em;
    font-weight: 500;
  }
}

.h2 {
  @include heading-large($font-size: 1.75rem, $color: inherit);

  @include medium {
    @include heading-large($font-size: 2.1875rem, $color: inherit);
  }

  &--color-white {
    @include heading-large($color: $white);
  }
}

.h2-small {
  @include heading-large($font-size: 1.5rem, $color: inherit);
}

.h3 {
  @include heading-medium($font-size: 1.6rem, $color: inherit);

  @include medium {
    @include heading-medium($font-size: 2rem, $color: inherit);
  }

  &--color-white {
    @include heading-medium($color: $white);
  }

  &--primary {
    @include heading-medium($color: clr(primary), $font-size: 1.8rem);

    border-left: 5px solid clr(primary);
    padding-left: 1rem;
    margin-bottom: 2rem;
  }
}

.h3-large {
  @include heading-medium($font-size: 2.4rem, $color: inherit);
}

.h4 {
  @include heading-small($font-size: 1.3rem, $color: inherit);

  @include medium {
    @include heading-small($font-size: 1.625rem, $color: inherit);
  }

  &--color-white {
    @include heading-small($color: $white);
  }
}

.h5 {
  @include heading-xs($color: inherit);
}

.h6 {
  @include heading-xxs($color: inherit);
}

/* TODO: BEM selectors should be applied to wysiwyg-created content */

h1 {
  @extend .h1;
}

h2 {
  @extend .h2;
}

h3 {
  @extend .h3;
}

h4 {
  @extend .h4;
}

h5 {
  @extend .h5;
}

h6 {
  @extend .h6;
}
