// Sass map to define colors. Each set should have a "Dark" variation when required.
// e.g. `default` and `default-dark`. Or `purple` and `purple-dark`.
$button-colors: (
  primary: (text: clr(text-inverse),
    bg: var(--main-page-color),
    text-hover: var(--main-page-color),
    bg-hover: clr(background),
    br-color: var(--main-page-color),
    br-color-hover: var(--main-page-color),
  ),
  outlineprimary: (text: var(--main-page-color),
    bg: clr(background),
    text-hover: clr(background),
    bg-hover: var(--main-page-color),
    br-color: var(--main-page-color),
    br-color-hover: var(--main-page-color),
  ),
  secondary: (text: clr(text-inverse),
    bg: clr(secondary),
    text-hover: clr(secondary),
    bg-hover: clr(background),
    br-color: clr(secondary),
    br-color-hover: clr(secondary),
  ),
  outlinesecondary: (text: clr(primary),
    bg: clr(background),
    text-hover: clr(background),
    bg-hover: clr(primary),
    br-color: clr(primary),
    br-color-hover: clr(primary),
  ),
  tertiary: (text: clr(text-inverse),
    bg: clr(tertiary),
    text-hover: clr(tertiary),
    bg-hover: clr(background),
    br-color: clr(tertiary),
    br-color-hover: clr(tertiary),
  ),
  outlinetertiary: (text: clr(tertiary),
    bg: clr(background),
    text-hover: clr(background),
    bg-hover: clr(tertiary),
    br-color: clr(tertiary),
    br-color-hover: clr(tertiary),
  ),
  link: (text: clr(primary),
    bg: clr(transparent),
    text-hover: clr(red-600),
    bg-hover: transparent,
    br-color: transparent,
    br-color-hover: transparent,
  ),
);

// Mixin to define colors for one or more schemes.
// Simply pass the color scheme to the mixin to get the colors defined in the map.
// e.g. @include buttonColors(default) will get all of the colors defined in the "default" section.
@mixin buttonColors($scheme,
  $pallate: map-get($button-colors, $scheme),
  $map: $button-colors) {
  color: map-get($pallate, text);
  background-color: map-get($pallate, bg);
  border-color: map-get($pallate, br-color);

  &:visited {
    color: map-get($pallate, text);
  }

  &:hover {
    color: map-get($pallate, text-hover);
    background-color: map-get($pallate, bg-hover);
    border-color: map-get($pallate, br-color-hover);
  }
}

// The button-base mixin contains styles that apply to all buttons
// regardless of color or size.
@mixin button-base {
  border: 2px solid clr(primary);
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  font-family: $font-heading;
  text-transform: initial;
}

// Button color variations
// Note how we include dark mode options for each
@mixin button-color-primary {
  @include buttonColors(primary);
}

@mixin button-color-outline-primary {
  @include buttonColors(outlineprimary);
}

@mixin button-color-secondary {
  @include buttonColors(secondary);
}

@mixin button-color-outline-secondary {
  @include buttonColors(outlinesecondary);
}

@mixin button-color-tertiary {
  @include buttonColors(tertiary);
}

@mixin button-color-outline-tertiary {
  @include buttonColors(outlinetertiary);
}

@mixin button-color-link {
  @include buttonColors(link);
}

@mixin button-color-disabled {
  opacity: 1;
  transform: none;
  background-color: clr(background-disabled);
  border-color: clr(background-disabled);
  color: clr(form-input-text);
  cursor: not-allowed;
  box-shadow: none;

  &:hover,
  &:active,
  &:focus {
    border-color: clr(background-disabled);
    color: clr(form-input-text);
    background-color: clr(background-disabled);
    box-shadow: none;
  }
}

// Button size variations
@mixin button-sm {
  line-height: 1.4;
  padding: $space-1 $space-4;
}

@mixin button-md {
  line-height: 1.4;
  padding: $space-3 $space-8;
}

@mixin button-lg {
  line-height: 1.4;
  padding: $space-4 $space-12;
}

// Button width variations
@mixin button-auto {
  display: inline-block;
}

@mixin button-full {
  width: 100%;
}

.button {
  display: flex;
  justify-content: flex-start;

  &-content {
    background-color: var(--main-page-color);
    border: 1px solid $white;
    color: $white;
    font-weight: 900;
    font-size: 0.8rem;
    letter-spacing: 0;
    padding: 4px 6px;
    transition: all 0.3s;

    @include medium {
      font-size: 0.9rem;
      letter-spacing: 2px;
      padding: 8px 12px;
    }

    i {
      font-size: 1rem;
      margin-left: 5px;
    }

    &:hover {
      background-color: $white;
      color: var(--main-page-color);
    }
  }

  &--outline {
    .button-content {
      background-color: $white;
      border: 1px solid var(--main-page-color);
      color: var(--main-page-color);

      &:hover {
        background-color: var(--main-page-color);
        color: $white;
      }
    }
  }

  &--center {
    justify-content: center;
  }

  &--right {
    justify-content: flex-end;
  }
}

.button-orange {
  font-family: "Nunito", Sans-serif;
  font-size: 16px;
  font-weight: 600;
  fill: #1B3263;
  color: #1B3263;
  background-color: #FABA5B;
  border-radius: 40px 40px 40px 40px;
  display: inline-block;
  line-height: 1;
  padding: 12px 24px;
  text-align: center;
  transition: all .3s;

  @keyframes elementor-animation-pulse {
    25% {
      transform: scale(1.1)
    }

    75% {
      transform: scale(0.9)
    }
  }

  &:hover {
    color: #FFFFFF;
    background-color: #1B3263;
    animation-name: elementor-animation-pulse;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}