.grid {
  @include grid;
}

.align-items-bottom {
  .layout-content {
    align-items: end;
  }
}

.align-items-end {
  .layout-content {
    justify-content: end;
  }
}

.align-items-center {
  .layout-content {
    text-align: center;
    align-items: center;
    justify-content: center;
  }
}

.align-items-start {
  .layout-content {
    align-items: start;
    justify-content: start;
  }
}

.grid-separator {
  .grid {
    .grid__item {
      position: relative;
      
      @include medium {
        &::after {
          content: "";
          width: 1px;
          height: 100%;
          background-color: lightgray;
          position: absolute;
          top: 0;
          right: -1rem;
        }
      }
  
      &:last-child {
        &::after {
          display: none;
        }
      }
    }
  }
}