.card-statistics {
  text-align: center;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  padding: 1rem 3rem 0rem 3rem;
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;

  @include small {
    border-right: 1px solid black;
  }

  &__number {
    color: #1B3263;
    font-size: 2rem;
    font-weight: 900;
    margin: 0;
  }

  &__heading,
  .heading {
    color: #FABA5B;
    font-size: 1.6em;
    font-weight: 800;
    line-height: 20px;
    margin: 0;
    margin-bottom: 1rem;
  }

  &__description {
    font-family: "Nunito", Sans-serif;
    font-size: 0.9rem;
    line-height: 19px;
  }
}