.card-modal {
  max-width: 400px;
  margin: 0 auto;

  &__content {
    cursor: pointer;
    position: relative;
    background-color: #164169;

    &-body {
      display: none;
    }

    &-img {
      img {
        &:hover {
          opacity: 0.5;
        }
      }
    }

    &-heading {
      position: absolute;
      padding: $space-3;
      min-height: 30%;
      width: 80%;
      text-align: left;
      background-color: $white;
      bottom: 0;
      right: 0;

      h3 {
        margin: 0;
        font-size: 1rem;
      }
    }
  }
}