.section-view-faculties {
  min-height: 350px;
  background-color: #1b3263;
  position: relative;
  color: $white;
  display: grid;
  gap: 15px;
  grid-template-columns: 1fr;
  padding: 40px 10px;
  place-items: center;

  @include medium {
    padding: 40px;
    grid-template-columns: repeat(2, 1fr);
  }

  .animated-element {
    width: 100%;
    transition: transform 0.5s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include medium {
      display: unset;
    }
  }

  &__title {
    display: grid;
    place-items: center;

    .heading {
      font-size: 1.75rem;
      font-weight: 700;
    }
  }

  &__view {
    position: relative;
    overflow: hidden;
    height: 250px;

    &-up {
      cursor: pointer;
      pointer-events: none;
      text-align: center;

      @include medium {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
      }

      svg {
        width: 20px;
        height: auto;
        fill: #818a91;

        path {
          fill: #818a91;
        }
      }
    }

    &-down {
      cursor: pointer;
      pointer-events: none;
      text-align: center;

      @include medium {
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        z-index: 1;
      }

      svg {
        width: 20px;
        height: auto;
        fill: #818a91;

        path {
          fill: #818a91;
        }
      }
    }

    &-content {
      position: relative;
      padding: 0 2rem;
      cursor: pointer;
      transition: transform 0.5s ease;

      @include medium {
        padding: 0;
        padding-bottom: 1rem;
      }

      .views-field-field-course-number-revision-id {
        color: $white;
        opacity: 0.7;
      }

      .views-row {
        width: max-content;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: auto 100%;
        grid-template-areas:
          'col1 col1'
          'col2 col3';
        margin-bottom: 3rem !important;

        .views-field {
          width: 100%;

          @include medium {
            width: max-content;
          }

          &:nth-child(1) {
            grid-area: col1;
          }

          &:nth-child(2) {
            display: flex;
            grid-area: col2;
            width: max-content;
            opacity: 0.7;

            &::after {
              content: '-';
              margin-left: 5px;
              margin-right: 5px;
            }
          }

          &:nth-child(3) {
            grid-area: col3;
            opacity: 0.7;
            width: 100%;
          }
        }

        .field-content,
        a,
        .views-field-field-course-number-revision-id {
          transition: all 0.2s ease-in-out;
          font-size: 0.88rem;
        }

        &:hover {
          opacity: 1;
          color: #fab95b;
          font-size: 20px;
          background: transparent;

          .field-content,
          a,
          .views-field-field-course-number-revision-id {
            color: #fab95b;
            font-size: 20px;
            opacity: 1;
          }

          .views-field {
            opacity: 1;
          }
        }
      }

      a {
        color: $white;
        font-size: 0.88rem;
        font-weight: bold;
        transition: all 0.2s ease-in-out;
        opacity: 0.7;
      }
    }
  }
}

.view-drupal {
  h2 {
    color: #1a3263;
    font-family: 'Nunito', Sans-serif;
    font-size: 1.5rem;
    font-weight: 800;
    line-height: 44px;
    letter-spacing: 0.5px;
    margin-bottom: 2rem;
    text-align: center;

    @include medium {
      text-align: left;
      font-size: 2.3rem;
    }

    @include large {
      font-size: 2.75rem;
    }
  }
}

.breadcrumb_course {
  position: relative;
  z-index: 2;
  @include layout-max-width;
  margin-bottom: -38px;
  padding-left: 1rem !important;
  padding-top: 15px !important;

  @include medium {
    padding-left: 0.1rem !important;
  }

  ol {
    li {
      color: #faba5b;
      font-size: 0.9rem;

      a {
        color: #faba5b !important;
      }

      &:last-child {
        color: #3d64ff !important;


      }
    }
  }

  &--course {
    ol li:last-child {
      color: white !important;
    }
  }
}

.opencourse-title {
  color: #1a3263;
  font-family: 'Nunito', Sans-serif;
  font-size: 2.3rem;
  font-weight: 800;
  line-height: 44px;
  letter-spacing: 0.5px;
  margin-bottom: 2rem;

  @include large {
    font-size: 2.75rem;
  }
}

.pager {
  &__item {

    &--first,
    &--last {
      display: none;
    }
  }

  &__link {
    font-family: "Nunito", sans-serif;
    display: inline-block;
    padding: 0;
    color: #666;
    background: transparent;
    font-size: 1.1rem;
    font-weight: 600;
    height: 30px;
    width: 30px;
    line-height: 30px;
    text-align: center;
    overflow: hidden;
    border-radius: 5px;

    &.is-active,
    &:hover,
    &:focus {
      background-color: #fab95b;
      color: #e61352;
      font-weight: 900;

      &:hover {
        background-color: #fab95b;
        color: #e61352;
        font-weight: 900;
      }
    }

    &--next {
      &::before {
        content: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--! Font Awesome Pro 6.4.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M278.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-160 160c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L210.7 256 73.4 118.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l160 160z"/></svg>');
        height: 16px;
        width: 8px;
        display: inline-block;
      }

      span {
        display: none;
      }
    }

    &--prev {
      &::before {
        content: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--! Font Awesome Pro 6.4.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M41.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l160 160c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L109.3 256 246.6 118.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-160 160z"/></svg>');
        height: 16px;
        width: 8px;
        display: inline-block;
      }

      span {
        display: none;
      }
    }
  }
}

.back-top {
  color: #fab95b;
  font-size: 40px;
  position: absolute;
  right: 20px;
  bottom: 10px;
  transition: .3s;
  width: 40px;
  height: 40px;

  svg {
    width: 40px;
    height: 40px;
  }
}