.accordion-item {

    &__content {
        .accordion-header {
            .accordion-button {
                display: flex;
                justify-content: flex-start;
                gap: 0.6rem;

                .accordion-item__heading {
                    width: 100%;
                }
                
                &::after {
                    display: none;
                }
            }
        }
    }

}