.card-faculty {
  width: 100%;
  padding: 24px;
  border: 1px solid rgba(204, 204, 204, 0.3);
  border-radius: 4px;
  display: flex;
  flex-direction: column;

  @include medium {
    flex-direction: row;
    justify-content: space-between;
    gap: 40px;
  }

  &__imagen {
    flex: 0 1 35%;
    overflow: hidden;

    img {
      transition: all 0.5s ease;
      transform: scale(1);
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 3px 3px 0 0;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  &__content {
    flex: 0 1 65%;

    i,
    svg {
      color: #e61352;
      fill: #e61352;
    }

    &-header {
      display: flex;
      flex-wrap: wrap;
    }

    &-details {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      margin-top: 10px;

      &-icon {
        display: flex;
        gap: 5px;
        color: #333;
      }
    }

    &-button {
      margin-bottom: 16px;
      margin-right: 20px;
      position: relative;

      &::after {
        position: absolute;
        top: 50%;
        right: -10px;
        width: 1px;
        height: 12px;
        opacity: 0.5;
        background: #999;
        content: "";
        transform: translate(0, -50%);
      }
    }

    &-link {
      position: relative;
      background-color: #3d64ff;
      font-size: 0.89rem;
      font-weight: 300;
      color: $white;
      display: inline-block;
      padding: 2px 50px 2px 15px;
      -webkit-clip-path: polygon(0% 0%, 100% 0, 88% 50%, 100% 100%, 0% 100%);
      clip-path: polygon(0% 0%, 100% 0, 88% 50%, 100% 100%, 0% 100%);

      &::after {
        position: absolute;
        top: 50%;
        right: -2px;
        width: 1px;
        height: 12px;
        opacity: 0.5;
        background: #999;
        content: "";
        transform: translate(0, -50%);
      }
    }

    &-title {
      color: #3d64ff;
      font-weight: 700;
      font-size: 0.7rem;
      letter-spacing: 2px;
      text-transform: uppercase;
      margin: 0;
      padding: 0;
      line-height: 25px;
    }

    &-heading {
      color: #333;
      font-size: 1rem;
      font-weight: 700;
      line-height: 24px;

      &:hover {
        color: #e61352;
      }
    }

    &-body {
      margin-top: 20px;
      margin-bottom: 28px;
      font-size: 1rem;
      color: #666;
      font-weight: 300;
      line-height: 26px;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    &-readmore {
      background-color: #e61352;
      padding: 9px 24px;
      border-radius: 3px;
      color: #fff;
      float: right;
      font-size: 0.85rem;
      font-weight: 400;
      line-height: 1;
      transition: all 0.3s;
      margin-right: 25px;

      &:hover {
        opacity: 0.5;
        color: #fff;
      }
    }
  }

  &--small {
    gap: 20px;
    border: none;
    padding: 0;

    @include medium {
      flex-direction: column;
    }

    .card-faculty__imagen {
      overflow: inherit;
    }

    .card-faculty__content {
      position: relative;
      padding: 15px 20px 12px 20px;
    }

    .card-faculty__content-button {
      position: absolute;
      top: -34px;
      right: 0;
      margin-right: 0;
    }

    .card-faculty__content-body,
    .card-faculty__button,
    .card-faculty__content-details {
      display: none;
    }

    .card-faculty__content-link {
      padding: 2px 12px 2px 22px;
      -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%, 12px 50%);
      clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%, 12px 50%);

    }

    .card-faculty__content-button::after {
      display: none;
    }
  }
}