.card-teacher {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  height: 100%;

  &__content {
    background-color: #1a3264;
    padding: 25px 20px 30px;
    width: 100%;
    height: 100%;
  }

  &__heading,
  .heading {
    font-size: 1.2rem;
    color: #fab95b;
  }

  &__image {
    overflow: hidden;
    position: relative;
    height: 250px;
    width: 100%;

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      z-index: 1;

      .card-teacher:hover & {
        background-color: rgba(0, 0, 0, .3);
      }
    }

    >div {
      width: 100%;
      height: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transition: all 0.5s ease;

      .card-teacher:hover & {
        transform: scale(1.1, 1.1);
      }
    }
  }

  &__description {
    color: $white;
    margin-bottom: 20px;
  }

  &__link {
    position: absolute;
    inset: 0;
    color: transparent;
    background-color: transparent;
    z-index: 2;
    opacity: 0;
  }

  &--teacherLink {
    transition: all .4s;
    transition-duration: 1500ms;

    .card-teacher__content {
      padding: 35px;
      text-align: center;
    }

    .card-teacher__link {
      position: unset;
      color: #FABB5A;
      margin-left: auto;
      margin-right: auto;
      border: 2px solid #FABB5A;
      font-size: 0.9rem;
      font-weight: 700;
      text-transform: uppercase;
      opacity: 1;
      padding: 10px 20px;
      border-radius: 2px;

      &:hover {
        color: #FFFFFF;
        background-color: #FABB5A;
      }
    }
  }
}