.form-item {
  color: clr(gray-500);
  margin-bottom: .5em;

  @include clearfix;
}

.form-item__label {
  display: block;
  font-weight: 400;
  font-size: .9rem;

  @include medium {
    display: block;
    margin-right: 2%;
    padding: 0.6em 0;
  }
}

.form-item__textfield {
  border-radius: 5px;
  border: 1px solid clr(gray-100);
  padding: 0.6em;
  max-width: 100%;

  &:focus {
    border-color: clr(gray-500);
  }

  &::placeholder {
    color: clr(gray-300);
  }

  &.form-submit {
    border-radius: unset;
  }
}

.form-item__description {
  margin-top: 0.3em;
}

.form-fieldset {
  border: none;
  margin-bottom: 2em;
  padding: 0;

  .form-item {
    margin-left: 1em;
    max-width: 31em;
  }
}

legend {
  &.h2 {
    color: clr(text);
    font-family: $font-heading;
    font-weight: 600;
    font-size: 1.1rem;
    line-height: 1.4;
    margin: 1rem 0 0.5rem;
  }
}

.form-actions {
  text-align: center;
  margin: 2rem 0;
}

.js-form-type-radio {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  line-height: 1.3;
  margin: 0;

  input {
    align-self: flex-start;
    margin-top: 10px;
  }
}

.js-form-item-title {
  .form-item__label {
    font-family: "Nunito",sans-serif;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 800;
    color: $blue;
    padding: 0.5rem;
  }
}

.js-form-item-form-checkbox {
  input {
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    margin-top: 13px;
  }

  .form-item__description {
    display: inline-block;
    width: 90%;
    vertical-align: middle;
  }
}
