.footer {
  background-color: #1B3263;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  padding: 4em 1rem;
  color: #fff;
  font-family: "Nunito", sans-serif;
  font-weight: 300;
  font-size: 15px;

  &__inner {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    flex-flow: column;
    gap: 1rem 40px;

    @include small {
      flex-wrap: wrap;
      flex-direction: row;
    }

    @include large {
      flex-wrap: nowrap;
    }
  }

  &__help {
    margin-bottom: $space;

    @include small {
      flex: 0 1 34.124%;
      margin-bottom: 0;
      margin-right: $space;
    }

    &-logo {
      margin-bottom: 20px
    }

    img {
      width: 33%;
      height: auto;
      -o-object-fit: contain;
      object-fit: contain;
    }
  }

  &__menu {
    width: 100%;

    @include small {
      flex: 0 1 33.971%;
    }
  }

  &__branding {
    width: 100%;

    @include small {
      flex: 0 1 31.569%;
      display: grid;
      place-items: center;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  a {
    color: #fff;
    text-decoration: none;
    font-size: 15px !important;
    font-weight: 300;
    line-height: 17px;
    font-family: "Nunito", sans-serif;
  }
}






