@mixin image {
  display: block;
  height: auto;
  max-width: 100%;
}

img,
picture {
  @include image;
}

figure {
  margin: 0; /* override normalize */
}

.figure__caption {
  font-style: italic;
}

.white-corners {
  margin-top: 30px;
  position: relative;
  display: inline-flex;

  @include medium {
    margin: 0;
  }

  &::after {
    content: "";
    border-top: 4px solid $white;
    border-left: 4px solid $white;
    width: 30px;
    height: 30px;
    position: absolute;
    z-index: 1;
    top: -15px;
    left: -0px;

    @include medium {
      top: -25px;
      left: -25px
    }

  }

  &::before {
    content: "";
    border-bottom: 4px solid $white;
    border-right: 4px solid $white;
    width: 30px;
    height: 30px;
    position: absolute;
    z-index: 1;
    bottom: -15px;
    right: 0;

    @include medium {
      bottom: -25px;
      right: -25px;
    }
  }
}
