// Container
.content-container {
  @extend .container;
}

.content-container-sm {
  @extend .container-sm;
}

.content-container-default {
  @extend .container-default;
}

.content-container-lg {
  @extend .container-lg;
}

.layout-twocol {
  .layout-content {
    display: block;

    @include medium {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  &.layout-twocol-33-67 .layout-content {
    @include medium {
      grid-template-columns: 31% 63%;
    }
  }

  &.layout-twocol-67-33 .layout-content {
    @include medium {
      grid-template-columns: 63% 31%;
    }
  }

  &.layout-twocol-25-75 .layout-content {
    @include medium {
      grid-template-columns: 25% 75%;
    }
  }

  &.layout-twocol-75-25 .layout-content {
    @include medium {
      grid-template-columns: 75% 25%;
    }
  }
}

.layout-threecol {
  .layout-content {
    display: grid;

    @include medium {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  &.layout-threecol-25-50-25 .layout-content {
    @include medium {
      grid-template-columns: 25% 50% 25%;
    }
  }

  &.layout-threecol-25-25-50 .layout-content {
    @include medium {
      grid-template-columns: 25% 25% 50%;
    }
  }

  &.layout-threecol-50-25-25 .layout-content {
    @include medium {
      grid-template-columns: 50% 25% 25%;
    }
  }
}

.layout-fourcol .layout-content {
  display: grid;

  @include medium {
    grid-template-columns: 25% 24% 24% 25%;
  }
}

