.ur-blog-viewed {
 
  &-item {
    font-weight: 600;

    &:hover {

      &::before {
        color: #282866;
      }

      a {
        text-decoration: underline;
      }
    }

    &::before {      
      content: "\f101";
      margin-right: 0.5rem;
      font-family: var(--fa-style-family, "Font Awesome 6 Free");
      font-weight: var(--fa-style, 900);
      color: #da0921;
      font-size: 1rem;

    }

  }

}