/* Tab Navigation */
.tabs__nav {
  @include list-reset;

  border-bottom: 1px solid clr(highlight-high);

  @include large {
    display: flex;
    min-width: 280px;
    max-width: 280px;
  }
}

/* Tab Content (hidden only in full #tabs js version) */
.tabs__tab {
  display: none;

  &.is-active {
    display: block;
  }
}

.tabs-component__tab {
  display: none;

  &.is-active {
    display: block;
  }
}

.tabs__content {
  padding: 0 1.5rem 1rem;
  width: 100%;

  .tabs__content-content {
    width: 100%;
  }
}

/* No-js fallback */
.tabs.no-js {
  .tabs__tab.is-active {
    display: block;
  }
}

/* No-js fallback */
.tabs-component.no-js {
  .tabs-component__tab.is-active {
    display: block;
  }
}

/* Drupal Local Tasks variant */
.tabs__nav--local-tasks {
  margin: 1em 0 0.5em;
  padding: 0;
}

.tabs-component__link--local-tasks {
  padding: 0.3em 1.5em;
}

.tabs-facultad {
  .nav {

    &-link {
      height: 80px;
      background-color: #dbdbe4;
      width: 100%;
      min-width: 160px;
      border-radius: 0;

      .tab-title {
        color: black;
      }

    }

    &-link.active {
      background-color: var(--main-page-color);

      .tab-title {
        color: $white;
      }
    }
  }

  .icon {
    width: 24px;
    height: 20px;
  }
}

.tabs, .tabs-component {
  display: none;

  @include medium {
    display: flex;
    
  }

  &__nav {
    flex-direction: column;
    list-style: none;
  }

  &__link {
    text-align: left;

    &.is-active {
      background-color: var(--main-page-color);
      color: $white;
      box-shadow: 2px 2px 2px rgb(0 0 0 / 10%);
      border: 2px solid $white;

      .tab-title__icon {
        color: $white;
      }

      .tab-title__text {
        color: $white;
      }
    }

    .tab-title {
      display: flex;
      align-items: center;
      gap: 1rem;

      &__icon {
        font-size: 2rem;
        color: var(--main-page-color);
      }

      &__text {
        line-height: 1.3;
        font-size: 1.2rem;
        color: clr(text);
      }
    }
  }
}

.tabs-component {

  &__content {
    margin-left: 15px;
  }

  &__link {
    &:hover {
      color: var(--c-text);
    }
  }

  &__link.is-active {
    &:hover {
      color: $white;
    }
  }
}

.tab-accordion-mobile {

  .accordion-item  {
    background-color: $white;
    border: none;
    padding: 10px 0;
  }

  .accordion-header {
    a {
      font-size: 1.1rem;
      text-decoration: none;
      color: var(--main-page-color);
      font-weight: 400;

      &:hover{
        color: var(--main-page-color);
      }
      
      span.fas {
        font-size: 1.8em;
      }

      i{
        float: right;
      }
    }
  }

  .accordion-header:not(.collapsed) {
    background-color: var(--main-page-color);
    padding: 10px;
    
    & > a {
      color: $white;
    }
  }

  .tab-content {
    padding: 1rem;
    width: 100%;
  }

  .tabs__link {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    &:hover {
      color: var(--main-page-color);
    }

    .tab__icon {
      margin-right: 10px;
      width: auto;
      img {
        width: 45px;
      }

      span {
        font-size: 30px;
        color: var(--main-page-color);
      }
    } 
  }

  .tabs__link.is-active {
      color: $white;

      & > span {
        color: $white;
      }

      &:hover {
        color: $white !important;
      }

      .tab__icon {
        img {
          filter: brightness(21.5);
        }

        span {
          color: $white;
        }
      }
  }
  
  @include medium {
    display: none;
  }
}


.tabs--vertical {
  .tabs__link {
    display: flex;
    align-items: center;
    
    &:hover {
      color: inherit;
    }
  }

    .tab__icon {
      margin-right: 10px;
      width: 50%;
      max-width: 45px;
      img {
        width: 45px;
      }

      span {
        font-size: 30px;
        color: var(--main-page-color);
      }
    }

    .tabs__link.is-active {
      color: $white;

      &:hover {
        color: $white !important;
      }

      .tab__icon {
        img {
          filter: brightness(21.5);
        }

        span {
          color: $white;
        }
      }
    }
}


.tabs--horizontal {
  .tabs__link {
    height: 100%;
    display: flex;
    align-items: center;
    
    &:hover {
      color: inherit;
    }

    .tab__icon {
      margin-right: 10px;
      width: auto;
      img {
        width: 45px;
      }

      span {
        font-size: 30px;
        color: var(--main-page-color);
      }
    }

    
  }
  
  .tabs__link.is-active {
      color: $white;

      &:hover {
        color: $white !important;
      }

      .tab__icon {
        img {
          filter: brightness(21.5);
        }

        span {
          color: $white;
        }
      }
    }
}



