.card-grid {
  @include grid;
}

.card-carousel {
  position: relative;

  @include medium {
    padding: 1.5rem 10px 10px;
  }

  .splide__arrow.splide__arrow--prev,
  .splide__arrow.splide__arrow--next {
    position: absolute;
    top: 50%;
    z-index: 1;
    visibility: visible;
    margin: 0;
    opacity: 1;
    width: 47px;
    height: 47px;
    line-height: 47px;
    font-size: 33px;
    border-radius: 50%;
    border: 1px solid black;
    background: transparent;
    display: grid;
    place-items: center;

    svg {
      width: 30px;
      height: 25px;
      transform: rotate(180deg);
    }

    &::before {
      font-family: $font-icon;
      color: #fff;
      font-size: 2rem;
      transition: all 0.4s;
    }

    &:hover {
      &::before {
        filter: brightness(0.5);
      }
    }
  }

  .splide__arrow.splide__arrow--prev {
    color: #18294b;
    font-weight: bold;
    left: -15px;

    @include small {
      left: -30px;
    }

    @include medium {
      left: -55px;
    }
  }

  .splide__arrow.splide__arrow--next {
    color: var(--main-page-color);
    right: -15px;

    @include small {
      right: -30px;
    }

    @include medium {
      right: -55px;
    }

    svg {
      transform: rotate(0);
    }
  }

  .splide__pagination:last-child {
    display: flex !important;
    bottom: -35px !important;
  }

  .splide__pagination__page {
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #585656;
    opacity: 0.4 !important;
  }

  .splide__pagination__page.is-active {
    opacity: 1 !important;
  }

  .splide__pagination {
    li {
      margin: 0 2px !important;
    }
  }

  &--carousel_show_mobile {
    display: block;

    @media only screen and (max-width:992px) {
      .splide__slide {
        width: calc(((100% + 3rem) / 1) - 3rem) !important;
        margin: 0 !important;
      }
    }

  }
}

.card-carousel--row-dowm-left {
  .splide__arrow--prev {
    margin: 0;
    top: 90%;
    left: 40%;

    @include medium {
      top: 85%;
      left: -7rem;
    }

    &::before {
      color: rgb(100, 100, 100) !important;
    }
  }

  .splide__arrow--next {
    margin: 0;
    left: 60%;
    top: 90%;

    @include medium {
      top: 85%;
      left: -4rem;
    }

    &::before {
      color: var(--main-page-color) !important;
    }
  }
}

