.menu-course {
  position: relative;
  background-color: #f0ece7;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  padding: 10px 0px 10px 0px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transition: all .5s ease;

  &__content {
    position: relative;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;

    @include medium {
      flex-wrap: unset;
      justify-content: space-between;
    }
  }

  &__imagen {
    width: calc(80% - 15px);
    order: 1;
    padding: 0 0.2rem;

    @include small {
      flex: 0 1 25%;
    }

    &:hover {
      img {
        transform: scale(1.02) translateX(2px);
      }
    }

    img {
      width: 100%;
      transition: 0.3s;
    }
  }

  .toggle-expand {
    padding: 0;
    display: block;

    @include medium {
      display: none;
    }

    &__open {
      display: flex;
      justify-content: flex-end;
    }

    &__icon {
      margin: 0;
      width: 2rem;
      height: 2rem;
      margin-left: auto;
    }
  }

  .main-nav {
    display: none;

    @include medium {
      display: block;
    }

    &.main-nav--open {
      display: block;
    }
  }

  &__search-form {
    width: 96%;
    order: 3;

    @include medium {
      width: max-content;
      order: 2;
      flex: 1 0 28%;
    }

    h2 {
      display: none;
    }

    form {
      width: 100%;
      display: flex;
      transition: 0.2s;
      overflow: hidden;
      border: 0 solid transparent;
      min-height: 33px;
      background-color: #e8e2db;
      border-radius: 162px;
      margin-bottom: 0;

      @include medium {
        width: max-content;
      }

      >div {
        margin: 0;
        padding: 0;

        &.form-item-title {
          width: 100%;
        }
      }
    }

    i {
      color: #315dd1;
    }

    input:not([type='submit']) {
      width: 100%;
      height: 100%;
      margin: 0;
      border: 0;
      padding: 0;
      display: inline-block;
      vertical-align: middle;
      white-space: normal;
      background: none;
      line-height: 1;
      font-size: 15px;
      appearance: none;
      -webkit-appearance: none;
      padding-left: calc(33px / 3);

      @include medium {
        width: unset;
      }

      &:valid {
        background-color: transparent !important;
      }

      &::placeholder {
        font-size: .9rem;
      }

      &:focus,
      &:focus-visible {
        border: none;
        outline: none;
        border-radius: 0;
        background-color: white !important;

        &::placeholder {
          color: #315dd1;
          opacity: 0.5;
        }
      }
    }

    .form-actions {
      position: relative;
    }

    input[type='submit'] {
      width: 33px;
      height: 100%;
      background-image: url('data:image/svg+xml;charset=utf-8,<svg%20aria-hidden="true"%20style="fill:%20%23315dd1;"%20class="svg-inline--fa%20fa-magnifying-glass"%20focusable="false"%20data-prefix="fas"%20data-icon="magnifying-glass"%20role="img"%20xmlns="http://www.w3.org/2000/svg"%20viewBox="0%200%20512%20512"%20data-fa-i2svg=""><path%20fill="%23315dd1"%20d="M500.3%20443.7l-119.7-119.7c27.22-40.41%2040.65-90.9%2033.46-144.7C401.8%2087.79%20326.8%2013.32%20235.2%201.723C99.01-15.51-15.51%2099.01%201.724%20235.2c11.6%2091.64%2086.08%20166.7%20177.6%20178.9c53.8%207.189%20104.3-6.236%20144.7-33.46l119.7%20119.7c15.62%2015.62%2040.95%2015.62%2056.57%200C515.9%20484.7%20515.9%20459.3%20500.3%20443.7zM79.1%20208c0-70.58%2057.42-128%20128-128s128%2057.42%20128%20128c0%2070.58-57.42%20128-128%20128S79.1%20278.6%2079.1%20208z"></path></svg>');
      background-size: 18px;
      background-repeat: no-repeat;
      background-color: #e8e2db;
      background-position: center;
      color: transparent;
      margin: 0;
      border: 0;
      padding: 0;
      display: inline-block;
      vertical-align: middle;
      white-space: normal;
      line-height: 1;
      font-size: 15px;
      appearance: none;
      -webkit-appearance: none;
      padding-right: calc(33px / 3);
      padding-left: calc(33px / 3);

      &:hover {
        background-color: white;
      }
    }
  }

  &__items {
    position: relative;
    width: calc(15% - 15px);
    display: flex;
    justify-content: flex-end;
    order: 2;

    @include small {
      width: max-content;
    }

    @include medium {
      flex: 0 1 45%;
      order: 3;
    }

    .toggle-expand__icon {
      width: 23px;
      height: auto;
      object-fit: cover;
    }

    .main-nav {
      position: absolute;
      right: -10px;
      top: 50px;
      left: unset;
      width: max-content;
      height: max-content;
      color: black;
      background-color: white;

      @include medium {
        position: static;
        background-color: transparent;
      }

      ul {
        display: flex;
        gap: 10px;
        flex-direction: column;

        @include medium {
          flex-direction: row;
        }
      }
    }

    a {
      color: #1a3263;
      font-weight: 700;
      margin-right: 10px;
      font-size: 0.9rem;

      @include medium {
        color: #1a3263;
      }

      &:hover,
      &.is-active {
        color: #315dd1;
      }

      &.white {
        color: black;

        @include medium {
          color: white;
        }
      }
    }
  }
}