@mixin link {
  color: clr(accent);

  &:hover {
    color: clr(accent-high);
  }
}

a {
  color: inherit;
  text-decoration: none;

  &:hover {
    color: inherit;
    text-decoration: none;
  }
}

a.button {
  font-size: 12px;
  text-decoration: none;
  background-color: var(--main-page-color);
  color: white;
  display: inline;
  padding: 10px;

  @include medium {
    font-size: 16px;
  }

  &#btn-download:hover {
    background: #192a4b;
    transition: 0.4s;
  }

  &:hover {
    color: white;
  }
}

.link {
  @include link;
}

.viewmore {
  text-transform: uppercase;
  color: $gray-800;
  text-decoration: none;

  &:hover {
    color: var(--main-page-color);
    text-decoration: underline;
  }

  &::after {
    color: var(--main-page-color);
    margin-left: 5px;
    font-weight: 900;
    font-family: $font-icon;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    line-height: 1;
    content: '\f054';
  }
}

.viewmorebox {
  text-transform: uppercase;
  color: $white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  background-color: var(--main-page-color);
  border: 2px solid var(--main-page-color);

  @include transition;

  &:hover {
    color: var(--main-page-color);
    background-color: transparent;
    border: 2px solid var(--main-page-color);

    @include transition;
  }

  &::after {
    @include font-awesome;

    content: '\f054';
    color: var(--main-page-color);
    margin-left: 5px;
    display: inline-block;
    line-height: 1;
  }
}

.wrap-bottom-more {
  text-align: right;
  padding: 3rem 0 0;
}

.news-tags {
  color: $sapphire;
  border: solid 2px $sapphire;
  padding: 0 10px;
  text-decoration: none;
  display: inline-block;

  a {
    color: $sapphire;
    text-decoration: none;
  }
}
