.tab-bootstap {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }

  .breaker-secretaria {
    margin: 50px;
  }

  .tab-content {
    width: 100%;
    padding: 5px 0;
  }

  .nav {
    flex-wrap: nowrap;
    box-shadow: 0px 0px 4px 0px #000;
    margin-bottom: 10px;

    &-tabs {
      border: none;
    }

    &-item {
      background-color: white;
      width: 100%;
    }

    &-link {
      padding: 0 15px;
      display: flex;
      align-items: center;
      text-align: left;
      color: #495057;
      margin: 0;
      font-size: 1.2rem;
      font-weight: 600;
      min-height: 60px;
      height: 100%;
      width: 100%;

      &:hover {
        isolation: auto;
      }

      img {
        width: 50px;
        height: 50px;
        margin: 1em;
      }

      span {
        color: var(--main-page-color);
        font-size: 2rem;
        margin-right: 5px;
      }

      a {
        margin-left: 10px;
        color: #131313;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
      }
    }

    &-link.active {
      color: #fff;
      background-color: var(--main-page-color);

      img {
        filter: invert(1) brightness(21.5);
      }

      span {
        color: white;
      }

      a {
        color: white;
      }
    }
  }

  .icontop & {
    &--vertical {
      justify-content: flex-start;

      .nav-link {
        a {
          justify-content: flex-start;
          text-align: left;
        }
      }
    }

    .nav {
      box-shadow: none;

      &-link {
        padding: 1rem;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-align: left;
        margin: 0;
        font-size: 1.2rem;
        font-weight: 500;
        min-height: 60px;
        height: 100%;
        width: 100%;

        &.active {
          img {
            filter: invert(1) brightness(21.5);
          }

          span {
            color: white;
          }
        }

        img {
          width: 50px;
          height: 50px;
        }

        span {
          color: var(--main-page-color);
          font-size: 2rem;
          margin-right: 5px;
        }

        a {
          margin-left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          font-size: .9rem;
          padding-top: .9rem;
          text-align: center;
        }
      }
    }

    .tab-content {
      .tab-bootstrap {
        .nav {
          box-shadow: none;

          &-link {
            padding: 1rem;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            text-align: left;
            margin: 0;
            font-size: 1.2rem;
            font-weight: 500;
            min-height: 60px;
            height: 100%;
            width: 100%;

            img {
              width: 50px;
              height: 50px;
            }

            span {
              color: var(--main-page-color);
              font-size: 2rem;
              margin-right: 5px;
            }

            a {
              margin-left: 0;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              width: 100%;
              height: 100%;
              font-size: 1.2rem;
              padding-top: 0;
            }
          }
        }
      }
    }
  }

  &__signs {
    position: relative;

    &:hover &-image {
      display: block;
    }

    i {
      border-radius: 50%;
      width: 20px;
      height: 20px;
      background-color: var(--main-page-color);
      color: white;
      display: grid;
      place-items: center;
      margin-right: 5px;
      font-size: 1rem !important;
    }

    &-image {
      position: absolute;
      left: 2em;
      top: 50%;
      transform: translateY(-50%);
      border: 3px solid var(--main-page-color);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      box-shadow: -3px 3px 3px #18181847;
      transition: all 0.3s ease-in-out;
      overflow: hidden;
      display: none;
      z-index: 2;

      img {
        width: 100px !important;
        height: 100px !important;
        margin: 0 !important;
        filter: none !important;
        object-fit: cover;
      }
    }
  }

  &--vertical {
    display: none;

    @media (min-width: 768px) {
      display: flex;
    }

    .nav {
      flex-direction: column;
      margin-right: 30px;
      height: 100%;

      &-tabs {
        box-shadow: none;
      }

      &-item {
        background-color: white;
        width: 280px;
        margin-bottom: 10px;
      }


      &-link {
        border: none;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        min-height: 80px;
        padding: 10px;
        transition: all 0.2s;

        &:hover {
          border: none;
        }
      }

      &-link.active {
        box-shadow: 2px 2px 2px rgb(0 0 0 / 10%);
        border: solid 2px white;
        transform: scale(1.03);
      }

      .link {
        min-height: 60px;
        text-align: left !important;
        justify-content: flex-start !important;
        font-family: 'Nunito';
        font-size: 1.2rem !important;
        font-weight: bold;
      }
    }

  }

  .coterminal & {
    &>div {
      background-color: rgba(255, 255, 255, 0.39);
      border-radius: 16px;
    }
  }

  &--course {
    .nav {
      box-shadow: none;
      margin-bottom: 40px;

      &-tabs {
        &-title {
          font-size: 1.6rem;
          font-weight: 700;
          color: #222222;
          margin-bottom: 1rem;
          font-family: "Nunito", sans-serif;
        }
      }

      &-item {
        background-color: transparent;
      }

      &-link {
        color: black;
        border-radius: 25px;
        width: max-content;
        min-height: unset;
        padding: 0;
        font-size: 1rem;

        &:hover,
        &:focus {
          border: none;
          isolation: initial;
        }

        a {
          padding: 6px 10px;
          margin-left: 0;

          @media (min-width:768px) {
            padding: 10px 50px;
            margin-left: 10px;
          }
        }

        &.active {
          background: #3d64ff;
          color: white;
          box-shadow: none;
          border: none;
        }
      }
    }


    &.tab-bootstap--vertical {
      display: block;

      @include medium {
        display: flex;
      }

      .nav {
        width: 100%;

        @include medium {
          width: 29.607%;
        }

        &-item {
          width: 100%;
        }

        &-link {
          flex-direction: column;
          align-items: flex-start;
          border-radius: 43px;
          margin: 20px 0 0;
          width: 100%;
          padding: 12px 24px;

          @include medium {
            margin: 20px 0;
          }

          &.active,
          &:hover {
            background-color: #fab95b;
            color: #222222;
          }

          &:focus {
            border: none;
          }

          .view {
            color: #222222;
            font-size: 0.7rem;
            font-weight: 400;
          }

          a {
            padding: 0 0 10px;
            color: #222222;
            min-height: unset;
            font-size: 0.75rem !important;
            margin: 0;
          }

          ;
        }
      }

      #tabContent {
        width: 100%;
      }

      &+.tab-accordion-mobile {
        display: none;

        .accordion-header {

          &:not(.collapsed) {
            background: #fab95b;
            border-radius: 43px;
          }

          a {
            color: #000;
            padding: 0 1rem;

            span {
              margin: 0 0 5px;
              display: block;
            }
          }

          .heading {
            .tab_item__title {
              font-size: 0.9rem !important;
              font-weight: bold;
            }

            .view {
              font-size: 0.9rem !important;
              font-weight: inherit;
              margin: 0;
              font-size: 1rem;
            }
          }
        }
      }

    }

    &+.tab-accordion-mobile {
      .nav-tabs-title {
        font-size: 1.8rem;
        font-weight: 600;
        margin-bottom: 1rem;
        padding: 1.2rem;
      }
    }

  }

  &--paginations {
    display: block;

    .nav-tabs {
      padding: 0 20px;
      position: relative;
      flex-wrap: wrap;

      @include medium {
        justify-content: flex-end;
      }

      &-title {
        order: -4;
        width: 100%;
        font-family: "Montserrat", sans-serif;
        font-size: 1.9rem;
        font-weight: 650;
        margin-bottom: 1rem;

        @include medium {
          width: max-content;
          order: unset;
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
      }

    }

    .nav-item {
      width: max-content;

      &--form {
        max-width: 70%;

        @include medium {
          max-width: max-content;
        }

        form {
          margin-right: 10px;

          input {
            font-family: Times;
            margin: 0;
            height: 100%;
            background-image: url('data:image/svg+xml;charset=utf-8,<svg%20aria-hidden="true"%20class="svg-inline--fa%20fa-magnifying-glass"%20focusable="false"%20data-prefix="fas"%20data-icon="magnifying-glass"%20role="img"%20xmlns="http://www.w3.org/2000/svg"%20viewBox="0%200%20512%20512"%20data-fa-i2svg=""><path%20d="M500.3%20443.7l-119.7-119.7c27.22-40.41%2040.65-90.9%2033.46-144.7C401.8%2087.79%20326.8%2013.32%20235.2%201.723C99.01-15.51-15.51%2099.01%201.724%20235.2c11.6%2091.64%2086.08%20166.7%20177.6%20178.9c53.8%207.189%20104.3-6.236%20144.7-33.46l119.7%20119.7c15.62%2015.62%2040.95%2015.62%2056.57%200C515.9%20484.7%20515.9%20459.3%20500.3%20443.7zM79.1%20208c0-70.58%2057.42-128%20128-128s128%2057.42%20128%20128c0%2070.58-57.42%20128-128%20128S79.1%20278.6%2079.1%20208z"></path></svg>');
            background-size: 14px;
            background-position: calc(100% - 15px);
            background-repeat: no-repeat;

            &::placeholder {
              color: #9b9898cc;
              font-size: 0.75rem;
            }
          }

          .js-form-item {
            margin: 0;
          }

          #edit-actions {
            margin: 0;
          }
        }
      }
    }

    .nav-link {
      background-color: transparent;
      border-radius: 5px;
      margin-left: 10px;

      img {
        width: 16px;
        height: 16px;
        object-fit: cover;
        margin: 0;
      }

      &.active {
        background: #f5f5f5;

        a {

          span,
          i,
          fill {
            color: #e61352;
          }

          img {
            filter: invert(0.3) sepia(100%) hue-rotate(333deg) saturate(47) brightness(80%);
          }

        }


      }

      a {
        padding: 8px;
        margin: 0;

        span,
        i,
        fill {
          color: black;
        }

      }
    }

    #tabContent {
      width: 100%;
    }

    &+.tab-accordion-mobile {
      display: none;
    }
  }

}



.fade:not(.show) {
  display: none;
}

.tab-accordion-mobile {

  .accordion-header {
    display: flex;
    align-items: center;
    padding: 0 10px;

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 55px;
      width: 100%;
      height: 100%;

      img {
        height: 24px;
        width: 24px;
      }

      .tab_item__title {
        width: 100%;
      }

      span {
        margin: 0 10px;
        font-size: 1.5rem;
      }

      i {
        font-size: 1.7rem;
      }
    }
  }
}